import Collection from './generic/collection'
import noUiSlider from 'nouislider'
import 'nouislider/distribute/nouislider.css'
import wNumb from 'wnumb/wNumb.js'

export default class Ranges extends Collection {
    ranges = [
        {
            'selector': '#range',
            'options': {
                start: [0, 100],
                connect: true,
                range: {
                    'min': 0,
                    'max': 100
                },
                margin: 23,
                tooltips: [wNumb({decimals: 0, prefix: ' ', suffix: ' $'}),
                    wNumb({decimals: 0, prefix: ' ', suffix: ' $'})],
            }
        },
    ];

    constructor() {
        super();
        this.init();
    }

    init() {
        this.ranges.forEach(range => {
            const ranges = document.querySelectorAll(range.selector);

            for (let rangeDOMElem of ranges) {
                const rangeSlider = new Range(rangeDOMElem, range.options);
                this.collection = rangeSlider;
            }
        });
    }
}

export class Range {
    instance;
    rangeInstance;

    /**
     *
     * @param sliderDOMElem{HTMLElement}
     * @param options{Object}
     * @return {Class}
     */
    constructor(sliderDOMElem, options) {
        this.instance = sliderDOMElem;
        this.rangeInstance = noUiSlider.create(sliderDOMElem, options);
    }
}